export function subscribeToNewBlock(provider, chainId, dispatch) {
  if (provider && chainId !== undefined) {
    var update = function update(blockNumber) {
      return dispatch({
        chainId: chainId,
        blockNumber: blockNumber
      });
    };

    provider.on('block', update);
    provider.getBlockNumber().then(function (blockNumber) {
      return update(blockNumber);
    }, function (err) {
      console.error(err);
    });
    return function () {
      provider.off('block', update);
    };
  }

  return function () {
    return undefined;
  };
}