import React, { useState, useEffect } from "react"
import Layout from '../layout'
import { Contract } from '@ethersproject/contracts'
import { useContractFunction, useCall } from '@usedapp/core'
import contractABI from "../contracts/Renkon.json";
import { utils } from 'ethers'
import { Renkon } from '../../gen/types'

import { ethers } from "ethers";

const whitelistEndpoint = '/list/';

const contractAddress = '0x11FDa544AAA0e8610382ceEe086ff5650237D4e2';


const getWhitelistProof = (address) => {
  return fetch(`${whitelistEndpoint}${address}.json`)
  .then(e => e.json())
  .then(e => {
    return e.proof;
  })
}

const getWindowEth = () => window !== undefined && window.ethereum;




const IndexPage = () => {

  const [displayAddress, setDisplayAddress] = useState('')
  const [signer, setSigner] = useState(null)
  const [proof, setProof] = useState([])
  const [contract, setContract] = useState(null)
  const [message, setMessage] = useState('');

  useEffect(() => {
    if(signer) {
      signer.getAddress().then(address => {
        setDisplayAddress(address.toLowerCase())
        getWhitelistProof(address)
        .then(e => {
          setProof(e)
        })
      })
    }
  }, [signer])

  const presaleMint = async () => {
    console.log(proof)
    const salePrice = await contract.presaleMint(proof, {
      value: 25000000
    })
    setMessage(JSON.stringify(salePrice))
  }

  const requestConnectWallet = async () => {
    const eth = getWindowEth()
    if(eth) {
      const provider = new ethers.providers.Web3Provider(eth)
      await provider.send("eth_requestAccounts", [])
      const signer = await provider.getSigner();
      const contract = new ethers.Contract(contractAddress, contractABI, signer) as Renkon
      setContract(contract)
      setSigner(signer);
    }
  }
  
  return (

    <Layout>

      {
        !signer && <>
        <button onClick={async () => { requestConnectWallet() }}>Please Connect Wallet</button>
      </>
      }

      {
        !!signer && <>
        <button onClick={async () => { requestConnectWallet() }}> { displayAddress } </button>
        {
          proof.length && <>
            You're on whitelist
            <button onClick={async () => { presaleMint() }}>Presale Mint</button>
          </>
        }
      </>
      }

      {
        message
      }
    </Layout>
  )
}

export default IndexPage
